import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Tooltip, Select, Collapse, Row, Col } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
// import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/supplier/courier/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange"

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;

const redirectTo = "/dashboard/setup/menus";

class Courier extends Component {
  state = {
    filter: {
      id_employee: null,
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getCouriers } = this.props;
    getCouriers();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.courier.error) {
      notification("error", this.props.courier.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.courier.success) {
      notification("success", this.props.courier.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getCouriers } = this.props;
    this.setState({
      filter: {
        id_employee: null,
        pagination: null
      }
    });
    getCouriers(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getCouriers } = this.props;
    this.setState({
      filter: {
        id_employee: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getCouriers();
  };

  //Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getCouriers } = this.props;
    //const { current_page } = this.props.courier;
    const { filter } = this.state;
    let string = "";

    if (filter.id_employee) {
      string += "id_related_employee=" + filter.id_employee;
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }
    // console.log(string);
    this.setState({ search: null });
    getCouriers(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getCouriers } = this.props;
    this.setState({
      filter: {
        id_employee: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getCouriers();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateCourier(id);
    } else {
      this.props.deactivateCourier(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getCouriers } = this.props;
    const { search } = this.props.courier;
    // const { filter } = this.props.courier;
    getCouriers(page, search);
  };

  listAction = {
    title: "",
    dataIndex: "id_courier",
    rowKey: "id_courier",
    // width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="couriers.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
            <Popconfirms
              title={<IntlMessages id="couriers.popconfirm.activate" />}
              onConfirm={this.handleRecord.bind(this, "activate", text)}
              okText={getLanguage({ id: "yes" })}
              cancelText={getLanguage({ id: "no" })}
            >
              <Tooltip
                placement="topRight"
                title={<IntlMessages id="tooltip.activate" />}
              >
                <Button size="small" className="invoiceRstBtn">
                  <i className="ion-refresh" />
                </Button>
              </Tooltip>
            </Popconfirms>
          )}
      </div>
    )
  };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "courier_name",
        rowKey: "courier_name",
        width: "50",
        // fixed: "left",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "label.description" }),
        dataIndex: "description",
        rowKey: "description",
        width: "15%",
        render: display => {
          if (display !== null) {
            return display.length > 50
              ?
              (
                <Tooltip placement="bottom" title={display}>
                  {display.substring(0, 50) + "..."}
                </Tooltip>
              )
              : display;
          } else {
            return "";
          }
        }
      },
      {
        title: "Internal",
        dataIndex: "is_internal",
        rowKey: "is_internal",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "primary";
            display = getLanguage({ id: "yes" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "no" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "couriers.filter.relatedEmployee" }),
        dataIndex: "related_employee",
        rowKey: "related_employee",
        width: "15%",
        render: text => {
          if (text !== null) {
            return <span>{text.first_name + (text.last_name ? " " + text.last_name : "")}</span>;
          } else {
            return <span />;
          }
        }
      },
      {
        title: getLanguage({ id: "vehicleNo" }),
        dataIndex: "vehicle_no",
        rowKey: "vehicle_no",
        width: "15%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "phone1" }),
        dataIndex: "phone1",
        rowKey: "phone1",
        width: "15%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "phone2" }),
        dataIndex: "phone2",
        rowKey: "phone2",
        width: "15%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "inactive" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];
    // console.log(this.props.courier);
    // console.log(this.state.filter);

    // Data setup
    const { couriers } = this.props.courier;
    //console.log(this.props.courier); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (permissions.includes("Update Courier") && columns.length === 8) {
      columns.push(this.listAction);
    }

    Object.keys(couriers).map((courier, index) => {
      return dataSource.push({
        ...couriers[courier],
        key: courier
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.courier;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="couriers.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Courier") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn">
                    <IntlMessages id="couriers.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={getLanguage({ id: "name" }) + ", " + getLanguage({ id: "label.description" }) + ", " + getLanguage({ id: "locations.add.phone" }) + ", " + getLanguage({ id: "vehicleNo" })}
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <div className="isoFilterContent">
                  <div style={{ width: "50%" }}>
                    <label>
                      <IntlMessages id="employees.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_employee"
                      )}
                      value={
                        this.state.filter.id_employee
                          ? this.state.filter.id_employee
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="couriers.filter.select.employee" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.courier.loadingFilterEmployees}
                      allowClear={true}
                    //showArrow={false}
                    >
                      {this.props.courier.employees.map((data, key) => (
                        <Option key={key} value={data.id_employee}>
                          {data.employee_code +
                            " (" +
                            data.first_name +
                            (data.last_name ? " " + data.last_name : "") +
                            ")"}
                        </Option>
                      ))}
                    </Select>
                  </div>

                  <div style={{ width: "15%" }}>
                    <label>
                      <IntlMessages id="dataPerPage" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "pagination"
                      )}
                      value={
                        this.state.filter.pagination
                          ? this.state.filter.pagination
                          : page_size
                      }
                      style={{ width: "70%" }}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={this.props.courier.loading}
                    //allowClear={true}
                    //showArrow={false}
                    >
                      <Option key={1} value={10}>
                        10
                      </Option>
                      <Option key={2} value={25}>
                        25
                      </Option>
                      <Option key={3} value={50}>
                        50
                      </Option>
                      <Option key={4} value={100}>
                        100
                      </Option>
                    </Select>
                  </div>

                  <div
                    style={{
                      width: "33%",
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                    >
                      <IntlMessages id="filter.submit" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </div>
                </div>
              </Panel>
            </Collapse>
          </div>

          <Row>
            <Col span={24}>
              <CardWrapper>
                {couriers.length === 0 && !this.props.courier.loading ? (
                  <Empty
                    description={<IntlMessages id="couriers.table.noData" />}
                  />
                ) : (
                    <div className="isoInvoiceTable">
                      {/* <Scrollbars style={{ width: "100%" }}> */}
                      <TableWrapper
                        dataSource={dataSource}
                        columns={columns}
                        loading={this.props.courier.loading}
                        className="invoiceListTable"
                        bordered={true}
                        // Set pagination, jika ada page_size mengunakan pagination API
                        // jika tidak ada page_size menggunakan pagination bawaan
                        pagination={
                          page_size
                            ? {
                              current: current_page,
                              pageSize: parseInt(page_size),
                              total: total,
                              onChange: this.handlePageChange.bind(this)
                            }
                            : true
                        }
                      />
                      {/* </Scrollbars> */}
                    </div>
                  )}
              </CardWrapper>
            </Col>
          </Row>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    courier: state.courier,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(Courier);
